/*----------------------------------------------
css settings for HTML div exactCenter 
------------------------------------------------*/

#abc {
  width: 100%;
  height: 100%;
  opacity: 1;
  top: 0;
  left: 0;
  display: none;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  overflow: auto;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

img#close {
  position: absolute;
  right: -14px;
  top: -14px;
  cursor: pointer;
}

div#popupContact {
  font-family: "Roboto", sans-serif;
}

form {
  position: relative;
  border: 2px solid var(--beerYellow);
  font-family: "Roboto", sans-serif;
  background-color: var(--mainBlack);
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto auto auto auto auto auto auto auto;
  padding: 50px;
  padding-top: 0;
  padding-bottom: 30px;
}

form *{
  margin: 0;
}

.payment-h2 {
  text-align: center;
  border-radius: 10px 10px 0 0;
  font-family: "Signation", sans-serif;
  font-size: 2.5rem;
  letter-spacing: var(--mainSpacing);
  color: var(--beerYellow);
}

input[type="number"],input[type="tel"],input[type="text"] {
  width: 82%;
  padding: 10px;
  border: 1px solid var(--beerYellow);
  font-size: 16px;
  font-family: "Roboto", sans-serif;
}

span.input-group-addon {
  color: red;
  font-weight: bold;
}
label {
  margin-bottom: 0.5em;
  margin-top: 1em;
}

.invalid{
  border: 2px solid red !important;
}

input+p,
label+p {
    display: none;
    margin: 0;
    margin-bottom: 10px;
    color: red;
}

input.invalid+p,
input.invalid+label+p {
    display: block;
}

.card-wrapper{
  grid-column: 1/3;
  grid-row: 1/2;
}

.payment-h2{
  grid-column: 1/3;
  grid-row: 2/3;
}

label[for=number]{
grid-column: 1/3;
grid-row: 3/4;
}

input[name=number]{
  grid-column: 1/3;
  grid-row: 4/5;
}

input[name=number]+p{
  grid-column: 1/3;
  grid-row: 5/6;
}

label[for=name]{
  grid-column: 1/3;
  grid-row: 6/7;
}

input[name=name]{
  grid-column: 1/3;
  grid-row: 7/8;
}

input[name=name]+p{
  grid-column: 1/3;
  grid-row: 8/9;
}

label[for=expiry]{
  grid-column: 1/2;
  grid-row: 9/10;
}

input[name=expiry]{
  grid-column: 1/2;
  grid-row: 10/11;
}

input[name=expiry]+p{
  grid-column: 1/2;
  grid-row: 11/12;
}

label[for=cvc]{
  grid-column: 2/3;
  grid-row: 9/10;
}

input[name=cvc]{
  grid-column: 2/3;
  grid-row: 10/11;
}

input[name=cvc]+p{
  grid-column: 2/3;
  grid-row: 11/12;
}

button[type=submit]{
  grid-column: 1/3;
  grid-row: 12/13;
  width: 50%;
  justify-self: center;
  place-self: center;
  margin-top: 30px;
}

.thanks{
  padding: 50px;
  padding-top: 0;
  border: 2px solid var(--beerYellow);
  background-color: var(--mainBlack);
  display: flex;
  flex-direction: row;
  display: none;
  text-align: center;
}

.thanks p{
  font-family: "Signation", sans-serif;
  font-size: 2.5rem;
  letter-spacing: var(--mainSpacing);
  color: var(--beerYellow);
}

@media only screen and (max-width: 600px) {

  #popupContact{
    width: 80%;
  }

  form{
    width: 80%;
}
}

